import { createSlice } from "@reduxjs/toolkit"
import { RootState } from "../../store"

interface State{
  projectModalOpen:boolean
}

const initialState:State={
  projectModalOpen:false
}

export const prejectListSlice=createSlice({
  name:'prejectListSlice',
  initialState,
  reducers:{
    openProjectModal(state){
      state.projectModalOpen=true
    },
    closeProjectModal(state){
      state.projectModalOpen=false 
    },
  },
})

export const prejectListActions=prejectListSlice.actions
export const selectProjectModalOpen=(state:RootState)=>state.projectList.projectModalOpen
