import React, { ReactNode } from 'react'
import * as auth from '../auth-provider'
import { User } from '../types/user'
import { useMount } from '../util'
import { http } from '../util/http'
import { useAsync } from '../util/useAsync'
import { FullPageErrorFallback, FullPageLoading } from '../components/lib'
import { useQueryClient } from 'react-query'

//主要是获得user并通过context让所有子组件使用users

export interface AuthForm{
  username:string,
  password:string
}

 export const bootstrapUser=async()=>{
  let user=null
  const token=auth.getToken()
  if(token){
    const data=await http('me',{token})
    user=data.user
  }
  return user
}

const AuthContext=React.createContext<{
  user:User|null,
  register:(form:AuthForm)=>Promise<void>,
  login:(form:AuthForm)=>Promise<void>,
  logout:()=>Promise<void>,
  }|undefined>(undefined)

AuthContext.displayName='AuthContext'

//AuthProvider的作用就是在使用AuthProvider时，











// 按照context的规则将value的值传递到各个子组件
export const AuthProvider=({children}:{children:ReactNode})=>{
  const {data:user,error,isLoading,isIdle,isError,run,setData:setUser}=useAsync<User|null>()

  const queryclient=useQueryClient()

  const login=(form:AuthForm)=>auth.login(form).then(user=>setUser(user))
  const register=(form:AuthForm)=>auth.register(form).then(user=>setUser(user))
  //这个函数主要用于在 React 组件中处理登出逻辑，并将用户信息从 React 状态中移除。
  const logout=()=>auth.logout().then(()=>{
     setUser(null)
    queryclient.clear()//使用react-query缓存中不清空会有之前的数据，请求的数据回来不及时就会显示之前用户的数据
  })
  
  //整个页面加载后,刷新后会导致user为null，那么需要在页面中将user保存
  //维持状态
  useMount(()=>{
    run(bootstrapUser())
  })

  if(isIdle||isLoading){
    return <FullPageLoading/>
  }

  if(isError){
    return <FullPageErrorFallback error={error}></FullPageErrorFallback>
  }

  return <AuthContext.Provider children={children} value={{user,login,register,logout}}/>

}



//自定义钩子，需要用到contextAPI时，直接使用useAuth
export const useAuth=()=>{
  const context=React.useContext(AuthContext)
  if(!context){
    throw new Error('useAuth必须在AuthProvider中使用')
    
  }
  return context
}